@import url(https://fonts.googleapis.com/css?family=Kaushan+Script&display=swap);
@import url(https://fonts.googleapis.com/css?family=Amatic+SC&display=swap);
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans&display=swap);

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Josefin Sans', sans-serif;
}
header {
  background: #e91e63;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
  display: flex;
  padding: 0 2rem;
  justify-content: space-between;
  align-items: center;
  height: 4rem;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
}

@media (max-width:768px) {
  header {
    padding: 0 1rem;
    height: 3.5rem;
  }
}

.logo {
  color: #fff;
  font-size: 1.5rem;
  font-family: 'Kaushan Script', cursive;
}

.logo-heart {
  padding-left: .4rem;
}

.search-box {
  display: flex;
  align-items: center;

}

.search-icon {
  color: #fff;
  background: #f73378;
  height: 2.2rem;
  width: 2.2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top-left-radius: .2rem;
  border-bottom-left-radius: .2rem;
  cursor: pointer;
}


.search-input input {
  height: 2.2rem;
  border-top-right-radius: .2rem;
  border-bottom-right-radius: .2rem;
  background: #f73378;
  outline: none;
  border: none;
  color: #fff;
  font-size: 1rem;
  font-family: 'Josefin Sans', sans-serif;
  -webkit-transition: all 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  transition: all 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  width: 200px;
}

@media (min-width:768px) {
  .search-input input:focus {
    width: 250px;
  }
}

@media (max-width:576px) {
  .search-input input {
    width: 150px;
    font-size: .9rem;
    height: 2rem;
  }

  .search-icon {
    height: 2rem;
    width: 2rem;
  }
}



.search-input input::-webkit-input-placeholder {
  color: #eeeeee;
}



.search-input input::-moz-placeholder {
  color: #eeeeee;
}



.search-input input:-ms-input-placeholder {
  color: #eeeeee;
}



.search-input input::-ms-input-placeholder {
  color: #eeeeee;
}



.search-input input::placeholder {
  color: #eeeeee;
}
.drinksPage {
  margin: 6rem auto 6rem auto;

  width: 1200px;
  max-width: 95%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

}

.drinkBox {
  text-align: center;
  margin: 1rem;
  box-shadow: 1px 2px 4px -1px rgba(0, 0, 0, 0.2), 1px 4px 5px 0px rgba(0, 0, 0, 0.14), 1px 1px 10px 0px rgba(0, 0, 0, 0.12);
  width: 250px;
  cursor: pointer;
  overflow: hidden;
  border-radius: .2rem;

  color: #000;

}




.drinkBox:hover {
  box-shadow: 1px 2px 4px -1px rgba(0, 0, 0, 0.4), 1px 4px 5px 0px rgba(0, 0, 0, 0.18), 1px 1px 10px 0px rgba(0, 0, 0, 0.16);
  width: 250px;
}

.drinkBox:hover .drink-img img:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.drink-img img {
  max-width: 100%;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: -webkit-transform .1s ease-in-out;
  transition: -webkit-transform .1s ease-in-out;
  transition: transform .1s ease-in-out;
  transition: transform .1s ease-in-out, -webkit-transform .1s ease-in-out;
}

.drink-img {
  overflow: hidden;
  width: 250px;
  height: 250px;
}

.drink-name {
  font-size: 1.5rem;
  margin: 1rem;


}
footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 4rem;
  background: #e91e63;
  box-shadow: 0px -2px 4px 1px rgba(0, 0, 0, 0.2), 0px -4px 5px 0px rgba(0, 0, 0, 0.14), 0px -1px 10px 0px rgba(0, 0, 0, 0.12);
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  z-index: 1000;
}

@media (max-width:768px) {
  footer {
    height: 3.5rem;
  }
}

.footer-text {
  color: #fff;
}

.footer-text p:nth-child(1) {
  margin-bottom: .5rem;
}
.drink {
  margin: 6rem auto;
  width: 1200px;
  max-width: 90%;

  box-shadow: 1px 2px 4px -1px rgba(0, 0, 0, 0.2), 1px 4px 5px 0px rgba(0, 0, 0, 0.14), 1px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.item-content {
  padding: 1rem;
}

.item-img img {
  width: 250px;
}

@media (max-width:768px) {
  .item-img img {
    width: 100%;
  }
}

.item-header {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-items: center;
  margin-bottom: 2rem;
}

@media (max-width:768px) {
  .item-header {
    grid-template-columns: 1fr;
    grid-row-gap: 1rem;
  }
}

.item-name {
  text-align: center;
}

.item-gap {
  margin-bottom: 1rem;
}
